import { Component, OnInit, ViewChild, ElementRef, Input, Output, EventEmitter, OnDestroy, ChangeDetectionStrategy, ChangeDetectorRef, ViewEncapsulation } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor, FormControl } from '@angular/forms';
import { DatepickerService } from './datepicker.service';

@Component({
  selector: 'anglar-datepicker',
  templateUrl: './datepicker.component.html',
  styleUrls: ['./datepicker.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  providers: [{
    provide: NG_VALUE_ACCESSOR,
    useExisting: DatepickerComponent,
    multi: true
  }]
})
export class DatepickerComponent implements OnInit, ControlValueAccessor, OnDestroy {
  constructor(
    public datePickerService: DatepickerService,
    public changeDetect: ChangeDetectorRef) { }

  @Input('position') position: string = 'bottom';
  @Input('dateFormat') dateFormat: string = 'DD-MMM-YYYY';
  pickerHeight: number = 500;

  @Output() onDateSelect:any = new EventEmitter();
  
  @ViewChild('pickerCompRef') pickerCompRef: ElementRef;
  @ViewChild('pickerInput') pickerInput: ElementRef;
  
  elementTop: any;
  elementLeft: any;
  inputOffset: number = 26;
  positioned: boolean = false;

  hide: boolean = true;
  items: any;
  dateComposer: any;
  months: any;
  month: number;
  year: any;
  years: any;


  myInput: any = '';
  onChange;
  onBlur;
  ngOnInit() {
    this.items = ['S', 'M', 'T', 'W', 'T', 'F', 'S'];
    this.months = this.datePickerService.getMonths();
    this.years = this.datePickerService.getYears();
    this.year = this.datePickerService.getYear();
    this.month = this.datePickerService.getMonth();
    this.genCalendarView();
    document.body.appendChild(this.pickerCompRef.nativeElement);
    this.detectChanges();
  }

  positionBody() {
    if(!this.positioned) {
      this.positionElement();
    }
    this.openPicker();
  }

  /**
   * This is where the values updated in view in parent component gets updated in the child component here
   * @param myInput 
   */
  writeValue(myInput: any): void {
    this.myInput = myInput;
    this.detectChanges();
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onBlur = fn;
  }

  positionElement() {
    let dropdownItem = this.pickerCompRef.nativeElement;
    dropdownItem['style']['top'] = this.elementTop + 'px';
    dropdownItem['style']['left'] = this.elementLeft + 'px';
    this.positioned = true;
  }

  getPosition() {
    let rect = this.pickerInput.nativeElement.getBoundingClientRect();
    let scrollTop = document.documentElement.scrollTop ?
      document.documentElement.scrollTop : document.body.scrollTop;
    let scrollLeft = document.documentElement.scrollLeft ?
      document.documentElement.scrollLeft : document.body.scrollLeft;
    this.elementTop = rect.top + scrollTop;
    this.elementLeft = rect.left + scrollLeft;
    switch(this.position) {
      case 'left':
        this.elementLeft -= 300;
        this.elementTop -= this.inputOffset - 20;
      break;
      case 'right':
        this.elementLeft += 200;
        this.elementTop -= this.inputOffset - 20;
      break;
      case 'top':
        this.elementTop -= 273;
      break;
      case 'bottom':
      default:
      this.elementTop += this.inputOffset;
      break;
    }
  }

  ngOnDestroy() {
    this.pickerCompRef.nativeElement.outerHTML = '';
  }

  onDateSelection(day: any) {
    if(day < 10) {
      day = '0'+day;
    }
    let month: any = this.month;
    if(this.month < 10) {
      month = '0'+this.month;
    }
    let selectedDate = this.year + '-' + month + '-' + day;
    let dateObj =  {
      formattedDate: this.datePickerService.getDateFormatted(selectedDate, this.dateFormat),
      dateObject: this.datePickerService.getDateMoment(selectedDate)
    };
    this.myInput = dateObj.formattedDate;
    this.onChange(this.myInput);
    this.onDateSelect.emit(dateObj.dateObject);
    this.closePicker();
  }

  genCalendarView() {
    try {
      let startDay = this.datePickerService.generateMonthDate(this.month, this.year);
      let lastDay = this.datePickerService.getMonthDates(this.month, this.year);
      try {
        this.dateComposer = this.datePickerService.dateComposer(startDay, lastDay);
        this.detectChanges();
      } catch (e) {
        console.error(e);
      }
    } catch (e) {
      console.error(e);
    }
  }

  gotoPrevMonth() {
    if( Number(this.month) === 1) {
        this.month = 12;
        this.year--;
    } else {
      this.month--;
    }    
  }

  gotoNextMonth() {
    if( Number(this.month) === 12) {
        this.month = 1;
        this.year++;
    } else {
      this.month++;
    }
  }

  openPicker() {
    this.hide = false;
    this.detectChanges();
  }

  closePicker() {
    this.hide = true;
    this.detectChanges();
  }

  detectChanges() {
    this.changeDetect.detectChanges();
  }

}
